.body_background {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-transition: all .9s ease-in-out;
    -o-transition: all .9s ease-in-out;
    transition: all .9s ease-in-out;
    z-index: -1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-perspective: 1000;
            perspective: 1000;
}

.body_background.active {
    opacity: 1;
    visibility: visible;
}

.body_background--morning {
    background-image: -webkit-gradient(linear, left bottom, left top, from(#37ecba), to(#72afd3));
    background-image: -webkit-linear-gradient(bottom, #37ecba 0%, #72afd3 100%);
    background-image: -o-linear-gradient(bottom, #37ecba 0%, #72afd3 100%);
    background-image: linear-gradient(to top, #37ecba 0%, #72afd3 100%);
}

.body_background--afternoon {
    background-image: -webkit-gradient(linear, left bottom, left top, from(#4facfe), color-stop(60%, #00f2fe), to(#fbffe6));
    background-image: -webkit-linear-gradient(bottom, #4facfe 0%, #00f2fe 60%, #fbffe6 100%);
    background-image: -o-linear-gradient(bottom, #4facfe 0%, #00f2fe 60%, #fbffe6 100%);
    background-image: linear-gradient(to top, #4facfe 0%, #00f2fe 60%, #fbffe6 100%);
}

.body_background--evening {
    background-image: -webkit-gradient(linear, left bottom, left top, from(#fee140), to(#fa709a));
    background-image: -webkit-linear-gradient(bottom, #fee140 0%, #fa709a 100%);
    background-image: -o-linear-gradient(bottom, #fee140 0%, #fa709a 100%);
    background-image: linear-gradient(to top, #fee140 0%, #fa709a 100%);
}

.body_background--night {
    background-image: -webkit-gradient(linear, left bottom, left top, from(#e00f21), color-stop(40%, #284384), to(#284384));
    background-image: -webkit-linear-gradient(bottom, #e00f21 0%, #284384 40%, #284384 100%);
    background-image: -o-linear-gradient(bottom, #e00f21 0%, #284384 40%, #284384 100%);
    background-image: linear-gradient(to top, #e00f21 0%, #284384 40%, #284384 100%);
}

